<template>
  <div class="container">
    <div class="header">
      <el-button icon="el-icon-back" circle size="mini" @click="goBack" class="back-btn"></el-button>
      <h1 class="page-title">{{ monthTitle }}收支明细</h1>
    </div>
    <div class="days">
      <div class="days-item" v-for="item in dailyDetails" :key="item.date" @click="goToDetail(item.date)">
        <div class="days-item-title">{{ item.date }}日</div>
        <div class="days-item-content">
          <div class="income">收入：{{ item.income }}元</div>
          <div class="expense">支出：{{ item.expense }}元</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MonthDetail',
  data() {
    return {
      monthTitle: '',
      dailyDetails: []
    }
  },
  created() {
    const monthId = this.$route.params.id
    this.monthTitle = this.getMonthTitle(monthId)
    this.generateDailyData(monthId)
  },
  methods: {
    getMonthTitle(monthId) {
      const months = ['一月', '二月', '三月', '四月', '五月', '六月', 
                     '七月', '八月', '九月', '十月', '十一月', '十二月']
      return months[monthId - 1]
    },
    
    generateDailyData(monthId) {
      const daysInMonth = new Date(2025, monthId, 0).getDate()
      this.dailyDetails = Array.from({ length: daysInMonth }, (_, index) => ({
        date: index + 1,
        income: Math.floor(Math.random() * 500) + 100,
        expense: Math.floor(Math.random() * 300) + 50
      }))
    },
    goBack() {
      this.$router.push('/home')
    },
    goToDetail(day) {
      this.$router.push({
        name: 'DayDetail',
        params: { 
          id: this.$route.params.id,
          day: day
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
.container {
  padding: 20px;
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-bottom: 30px;
}

.back-btn {
  position: absolute;
  left: 20px;
}

.page-title {
  text-align: center;
  color: #666;
  font-size: 30px;
  margin: 0;
}

.days {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 15px;
  padding: 20px;
  
  &-item {
    background: #fff;
    border-radius: 8px;
    padding: 15px;
    box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
    
    &:hover {
      transform: translateY(-3px);
      box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
    }
    
    &-title {
      font-size: 16px;
      font-weight: bold;
      margin-bottom: 10px;
      color: #333;
    }
    
    &-content {
      .income {
        color: #67C23A;
        margin-bottom: 5px;
      }
      
      .expense {
        color: #F56C6C;
      }
    }
  }
}
</style> 